<template>
<div>
    <div class="heading">
        <h4>Sargam A Day</h4>
        <h3>Keeps All Gham Away</h3>
    </div>
    <input type="hidden" id="testing-code" :value="shareText">
    <div id="app">
        <v-card class="mx-auto my-12"
    max-width="374" v-if="clips.length > 0">
    <v-img
            src="/music_rainbow.jpeg"
            height="250"
          />
            <v-card-title>Contributor: {{getClipContributor()}}</v-card-title>
            <v-btn @click="phoneShare()">Share on Phone</v-btn>
                <v-row v-for="(hint, index) in clips[offset].hints" :key="index" v-show="hintsRevealed[index]">
                  <v-col>
                      <audio controls v-if="clips[offset].hints[index].url">
                        <source :src="clips[offset].hints[index].url" type="audio/mpeg">
                        Your browser does not support the audio tag.
                      </audio>
                      <span v-if="clips[offset].hints[index].text">{{clips[offset].hints[index].text}}</span>
                   </v-col>
                   <v-col v-if="index < clips[offset].hints.length-1">
                       <v-btn @click="hintsRevealed[index+1]=true">{{getHintText(index+1)}}</v-btn>
                   </v-col>
                </v-row>
                <v-row>
                    <v-col>
                        <v-text-field label="Answer" v-model="answer" clearable v-if="!latestPlayed"></v-text-field>
                        <v-btn @click="evaluate()" v-if="!latestPlayed">Enter</v-btn>
                        <span v-if="latestPlayed">Next Sargam Tomorrow !</span>
                    </v-col>
                </v-row>
        </v-card>

        <v-dialog
          v-model="overlayShow"
        >
        <v-card class="triangle-card">
          <v-card-title class="page-title text-no-wrap pt-2">
              {{winMessage}} 
            </v-card-title>
            <v-card-actions>
            <v-row>
              <v-col>
            <a :href="whatsAppLink" data-action="share/whatsapp/share" target="_blank"><v-icon x-large color="success">mdi-whatsapp</v-icon></a>   
        
          </v-col>
          </v-row>
          <v-row>
              <v-col>
            <v-btn
              color="primary"
              text
              @click="overlayShow = false"
            >
              Close
            </v-btn>
              </v-col>
            </v-row>
          </v-card-actions>
        </v-card>
        </v-dialog>
    </div>
</div>
</template>
<script>
export default{
    mounted: async function(){
        const jsonURL = "https://saregama.s3.ap-south-1.amazonaws.com/clips.json";
        const response = await fetch(jsonURL);
        const data = await response.json();
        this.clips = data;
        this.offset = this.getOffset();
        if (this.contestNumber == localStorage.getItem("LAST_CONTEST")){
            this.getShareMessage();
            this.overlayShow = true;
            this.latestPlayed = true;
        }
    },
    data: function() {
      return {
        baseDate: '2022-02-06',
        clips: [],
        answer: '',
        offset: 0,
        hintsRevealed: {0: true, 1: false, 2: false},
        contestNumber: 1,
        shareText: "",
        gold: '',
        silver: '',
        bronze: '',
        participation: '',
        whatsAppUrl: 'whatsapp://send?',
        whatsAppText: 'text=SargamADay ',
        whatsAppLink: '',
        overlayAbsolute: true,
        overlayShow : false,
        overlayZIndex: 0,
        GOLD: 0,
        SILVER: 1,
        BRONZE: 2,
        PARTICIPATION: 3,
        display: '',
        winMessage: '',
        latestPlayed: false,
        nextContestMessage: 'Next Sargam Tomorrow !'

      }
    },
    methods: {
    share: function(){
        let testingCodeToCopy = document.querySelector('#testing-code')
        testingCodeToCopy.setAttribute('type', 'text')
        testingCodeToCopy.value = this.silver
        testingCodeToCopy.select()
        document.execCommand('copy');
        testingCodeToCopy.setAttribute('type', 'hidden')
    },
    phoneShare: function(){
      console.log(navigator);
      navigator
      .share({
        text: 'Hello World',  
      })
    .then(() => alert('Successful share! '))
    .catch(err => alert(err));
    },
    getClipURL: function(){
        return this.clips[this.offset].url;
    },
    getHintText: function(idx){
      if (idx == 0) return "Gold Hint";
      if (idx == 1) return "Silver Hint";
      if (idx == 2) return "Bronze Hint";
    },
    getClipContributor: function(){
      if (this.clips.length == 0) return "";
      return this.clips[this.offset].contributor;
    },    
    getOffset: function(){
        const baseDate = new Date(this.baseDate);
        const currentDate = new Date();
        this.contestNumber = currentDate.getDate() - baseDate.getDate();
        const offset = this.contestNumber % this.clips.length;
        return offset;
    },       
    playSound: function () {
      const sound = this.clips[this.offset].url; 
      if(sound) {
        var audio = new Audio(sound);
        audio.play();
      }
    },
    getShareMessage: function(){
        let displayStr = this.whatsAppUrl;
        let medalWon = localStorage.getItem("LAST_MEDAL")*1;
        let medalTally = JSON.parse(localStorage.getItem("MEDAL_TALLY"));
        this.display = this.whatsAppText;
        this.display += `%0a`;
        if (medalWon == this.GOLD){
            this.winMessage = 'You Won Gold Medal ';
        }
        else if (medalWon == this.SILVER){
            this.winMessage = 'You Won Silver Medal ';
        }
        else if (medalWon == this.BRONZE){
            this.winMessage = 'You Won Bronze Medal ';
        }
        else {
            this.winMessage = 'You Won Participation Medal ';
        }
        this.display += this.winMessage;
        this.display += `%0a`;
        this.display += 'Overall Medal Tally ';
        this.display += `%0a`;
        this.display += ' ' + medalTally["GOLD"];
        this.display += `%0a`;
        this.display += ' ' + medalTally["SILVER"];
        this.display += `%0a`;
        this.display += ' ' + medalTally["BRONZE"];
        this.display += `%0a`;
        this.display += ' ' + medalTally["PARTICIPATION"];
        displayStr += this.display;
        return displayStr;
    },
    answerMatch: function(){
      const answers = this.clips[0].answers;
      const userAnswerArr = this.answer.trim().split(" ");
      let allKeywordsMatch = true;
      for (const keywordArr of answers){
        let keywordPresent = false;
        for (const keyword of keywordArr){
          const index = userAnswerArr.findIndex(element => {
            return element.toLowerCase() === keyword.toLowerCase();
          });
          if (index > -1) keywordPresent = true;
        }
        if (!keywordPresent){
          console.log("keyword missing", keywordArr)
          allKeywordsMatch = false;
        }
      }
      console.log("allKeywordsMatch", allKeywordsMatch)
      return allKeywordsMatch ? 1 : -1;
    },
    evaluate: function(){
        if (this.latestPlayed) return;
        this.answer = this.answer.trim();
        
        const index = this.answerMatch();
        localStorage.setItem("LAST_CONTEST", this.contestNumber);
        let medalTallyJSONString = localStorage.getItem("MEDAL_TALLY");
        let medalTally;
        if (!medalTallyJSONString) {
            medalTally = {"GOLD": 0, "SILVER": 0, "BRONZE": 0, "PARTICIPATION": 0};
            localStorage.setItem("MEDAL_TALLY", JSON.stringify(medalTally));
        }
        else {
            medalTally = JSON.parse(medalTallyJSONString);
        }
        if (index > -1){
            if (this.hintsRevealed[2] == true){
                medalTally["BRONZE"] = medalTally["BRONZE"]*1 + 1;
                localStorage.setItem("LAST_MEDAL", this.BRONZE);
                
            }
            else if (this.hintsRevealed[1] == true){
                medalTally["SILVER"] = medalTally["SILVER"]*1 + 1;
                localStorage.setItem("LAST_MEDAL", this.SILVER);        
            }
            else {
                medalTally["GOLD"] = medalTally["GOLD"]*1 + 1;
                localStorage.setItem("LAST_MEDAL", this.GOLD);
            }
        }
        else {
            medalTally["PARTICIPATION"] = medalTally["PARTICIPATION"]*1 + 1;
            localStorage.setItem("LAST_MEDAL", this.PARTICIPATION);

        }
        localStorage.setItem("MEDAL_TALLY", JSON.stringify(medalTally));
        this.whatsAppLink = this.getShareMessage();
        this.overlayShow = true;
    }
  }
}

</script>
